.header {
  font-family: DM Sans, sans-serif;
}

.header__menu {
  z-index: 999;
  background-color: #fff;
  border: 2px solid #111;
  border-radius: 0 0 8px 8px;
  justify-content: space-between;
  margin: 0;
  padding: 18px 20px;
  display: flex;
  position: relative;
}

.header__aside {
  justify-content: center;
  gap: 14px;
  display: flex;
}

.header__button-open, .header__button-close {
  width: 28px;
  height: 28px;
  cursor: pointer;
  background-color: #fff;
  border: none;
  padding: 0;
}

.switch {
  width: 40px;
  height: 20px;
  display: inline-block;
  position: relative;
  top: 4px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  transition: all .4s;
  position: absolute;
  inset: 0;
  box-shadow: inset 1px 1px 2px #0000001a;
}

.slider:before {
  content: "";
  height: 18px;
  width: 18px;
  background: none, #f7f7f7;
  transition: all .4s;
  position: absolute;
  bottom: 1px;
  left: 1px;
}

input:checked + .slider:before {
  transform: translateX(20px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.logo {
  align-items: center;
  gap: 8px;
  display: flex;
}

.logo__icon {
  width: 24px;
  height: 24px;
}

.logo__text {
  width: 77px;
  height: 13px;
}

.header-modal {
  z-index: 99;
  width: 100vw;
  height: 100vh;
  background: radial-gradient(189.32% 190.93% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  position: fixed;
  top: 0;
}

.header-modal__nav {
  flex-direction: column;
  gap: 14px;
  margin-left: 31.7333%;
  display: flex;
}

.header-modal__home-btn {
  width: 72px;
  height: 34px;
  background: #eac645;
  border: none;
  border-radius: 36px;
  font-size: 14px;
  font-weight: 700;
}

.header-modal__home {
  color: #111;
  text-decoration: none;
}

.header-modal__shop-link {
  align-items: center;
  gap: 4px;
  text-decoration: none;
  display: flex;
}

.header-modal__shop-text {
  color: #fff;
  font-size: 14px;
  font-weight: 500;
}

.header-modal__shop-icon {
  width: 20px;
  height: 20px;
}

.is-hidden {
  display: none;
}

.profile {
  align-items: center;
  gap: 9px;
  margin: 0;
  padding: 87px 20px;
  display: flex;
}

.profile__photo {
  width: 37px;
  height: 37px;
  border-radius: 50%;
}

.profile__name {
  color: #fff;
  font-size: 22px;
  font-weight: 700;
}

